import moment from 'moment'

// property
export const Handle_CalendarDict = (q) => {
  const setObj = () => {
    // debugger
    var value = q.CurrentDay

    var month = value.format('M')
    var year = value.format('YYYY')

    var startDay = value.clone().startOf('month').startOf('week')
    var endDay = value.clone().endOf('month').endOf('week')
    var day = startDay.clone().subtract(1, 'day')

    var startWeek = value.clone().startOf('month').isoWeek()

    const calendar = []

    while (day.isBefore(endDay, 'day')) {
      calendar.push({
        days: Array(7)
          .fill(0)
          .map(() => day.add(1, 'day').clone()),
        week: day.isoWeek(),
      })
    }

    q.CalendarDict = {
      year,
      month,
      calendar,
    }
    q.IsGet_Calendar = false
  }

  // debugger
  var oldValue = q.calendarDict
  if (!oldValue) {
    setObj()
  } else {
    if (
      oldValue.month !== q.CurrentDay.format('M') ||
      oldValue.year !== q.CurrentDay.format('YYYY')
    ) {
      setObj()
    }
  }
}

// style
export const GetStyle = (q) => {
  return {
    width: '250px',
    item_height: '35px',
  }
}
