const pmData = () => PMData.Instance

// Instance
export const GetFromData = (data, dict) => {
  var qI = get(dict)

  qI._id = data.type
  qI.Type = data.type

  return qI
}

export const get = (dict) => {
  var qI = new Dict()

  return qI
}

