import {
  BehanceSquareFilled,
  CheckOutlined,
  GithubFilled,
  LinkedinFilled,
} from '@ant-design/icons'
import { Progress } from 'antd'
import { url } from './Base_Url'
import { Base } from './Base'
import './Base.css'

import * as api from '../../../../util/apiUtil'
import { Image } from '../../../image/Image'
import avatar from '../../../../image/Avatar.jpg'
import ImageView from '../../../image/ImageView'
import { PMData } from '../../single/model/PMData'

const pmData = () => PMData.Instance

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict)

  qI._id = data._id
  qI.Name = data.name

  return qI
}

export const get = (dict) => {
  var qI = new Base()

  qI.Dict = dict

  return qI
}

// Property

// view
export const GetView = (q) => {
  var lang = pmData().Language

  var image = new Image()
  image.Source = avatar
  image.Width = 80
  image.Alt = 'avatar'

  var divider = <div className="divider bg-sc-color mt-4 mb-4" />
  var titleRender = (v) => <div className="text-base font-medium mb-4">{v}</div>

  // language
  var circleprogress_Render = (p) => (
    <Progress
      type="circle"
      percent={p}
      width={48}
      strokeColor={'#ffc107'}
      strokeWidth={8}
      trailColor={'#000'}
      format={(p) => <span className="color">{p}%</span>}
    />
  )

  var languageRender = (data) => (
    <div key={data.Name} className="flex flex-col mx-3">
      <div className="text-center">{circleprogress_Render(data.Percent)}</div>
      <span className="text-xs mt-2 text-center">{data.Name}</span>
    </div>
  )

  return (
    <div className="w-60">
      {/* Avatar - Title */}
      <div className="cv__info__header p-6">
        <div className="flex flex-col items-center">
          <ImageView store={image} className="rounded-full" />
          <span className="font-medium mt-1.5 text-base">
            {{ vi: 'Hunh Thin Ti', en: 'Huynh Thien Tai' }[lang]}
          </span>
          <span className="flex flex-col items-center sc-color text-sm mt-2">
            <span>Fullstack Developer</span>
            <span>BIM Developer</span>
          </span>
        </div>
      </div>

      <div className="p-4">
        {/* Location */}
        <div>
          <table className="w-full">
            <tbody>
              <tr>
                <td className="font-medium">
                  {{ vi: 'Quc tch', en: 'Residence' }[lang]}:
                </td>
                <td className="text-right sc-color">
                  {{ vi: 'Vit Nam', en: 'Vietnam' }[lang]}
                </td>
              </tr>
              <tr>
                <td className="font-medium">
                  {{ vi: 'Thnh ph', en: 'City' }[lang]}:
                </td>
                <td className="text-right sc-color">
                  {{ vi: 'H Ch Minh', en: 'Ho Chi Minh' }[lang]}
                </td>
              </tr>
              <tr>
                <td className="font-medium">
                  {{ vi: 'Tui', en: 'Age' }[lang]}:
                </td>
                <td className="text-right sc-color">30</td>
              </tr>
            </tbody>
          </table>
        </div>

        {divider}

        {/* Language */}
        <div>
          {titleRender({ vi: 'Ngn ng', en: 'Languges' }[lang])}

          <div className="flex justify-evenly mt-4">
            {[
              {
                Name: { vi: 'Ting Vit', en: 'Vietnamese' }[lang],
                Percent: 100,
              },
              { Name: { vi: 'Ting Anh', en: 'English' }[lang], Percent: 75 },
            ].map((x) => languageRender(x))}
          </div>
        </div>

        {divider}

        {/* Knowledge */}
        <div>
          {titleRender({ vi: 'Kin thc', en: 'Knowledge' }[lang])}

          <div className="flex flex-col -mt-3">
            {[
              'antd, bootstrap',
              'mobx',
              'BIM',
              { vi: 'Thit k Kt cu', en: 'Strutural Design' }[lang],
              'Build of Quantity',
              'Wordpress, SEO',
            ].map((x) => (
              <div key={x} className="flex items-center text-xs my-1">
                <div className="o-color">
                  <CheckOutlined className="mr-3 -mt-0.5" />
                </div>
                <span className="sc-color">{x}</span>
              </div>
            ))}
          </div>
        </div>

        {divider}

        {/* Interest */}
        <div>
          {titleRender(
            { vi: 'am m - S thch', en: 'Interests - Hobbies' }[lang]
          )}

          <div className="flex flex-col -mt-3">
            {[
              { vi: 'Ti chnh, u t', en: 'Finance, Investing' }[lang],
              { vi: 'Pht trin bn thn', en: 'Self-help' }[lang],
              { vi: 'Chy b, Cu lng', en: 'Running, Badminton' }[lang],
            ].map((x) => (
              <div key={x} className="text-xs my-1">
                <span className="sc-color">{x}</span>
              </div>
            ))}
          </div>
        </div>

        {divider}

        {/* Contact */}
        <div>
          {titleRender({ vi: 'Lin lc', en: 'Contact' }[lang])}

          <table className="w-full -mt-2 mb-3">
            <tbody>
              <tr>
                <td className="font-medium">Tel:</td>
                <td className="text-right sc-color">+84 355 017 922</td>
              </tr>
              <tr>
                <td className="font-medium">Email:</td>
                <td className="text-right sc-color">bimtaiht@gmail.com</td>
              </tr>
            </tbody>
          </table>

          <div className="text-center">
            <div className="ml-3 flex text-base">
              <span className="mr-3">
                <LinkedinFilled />
              </span>
              <span>
                <GithubFilled />
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

// api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData)

  apiFunc()
    .then((response) => response.data)
    .then((data) => {
      if (handle) {
        handle(data)
      }
    })
}
