import { Body } from './Body'
import './Body.css'

import * as genUtil from '../../../../../util/generalUtil'
import * as comUtil from '../../../../../util/commandUtil'

import { PMDataVM } from '../../../single/form/PMDataVM'
import * as vt from '../../view/ViewType'
import { message } from 'antd'
import { PMData } from '../../../single/model/PMData'

const pmDataVM = () => PMDataVM.Instance
const pmData = () => PMData.Instance

// Instance
export const get = (dict) => {
  var qI = new Body()

  qI.Dict = dict

  return qI
}

// Property

// view
export const GetContentView = (q) => {
  var qI = pmDataVM()

  switch (qI.RouteData[0]) {
    case vt.home:
      return (
        <>
          <div className="flex mt-10">
            {/* Info */}
            <div className="bg-[#20202a] cv__info">{qI.Info.View}</div>

            {/* History */}
            <div className="flex-1 bg-[#1e1e28]">
              <div className="flex">
                <div className="w-1/2">
                  {/* Education */}
                  <div>{qI.Education.View}</div>

                  {/* Skill */}
                  <div>{qI.Skill.View}</div>
                </div>
                <div className="w-1/2">{qI.WorkHistory.View}</div>
              </div>
            </div>
          </div>
        </>
      )
  }
}

// main
export const GetMainView = (q) => {
  return <div className="mx-10 mt-2">{GetContentView(q)}</div>
}
