import { url } from './Base_Url'
import { Base } from './Base'
import './Base.css'

import * as itemDictUtil from '../../item/dict/DictUtil'
import * as itemUtil from '../../item/base/BaseUtil'

import * as api from '../../../../../../util/apiUtil'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict)

  qI._id = data._id
  qI.Name = data.name

  var itemDict = qI.ItemDict
  itemDict.Items = data.items.map((x) => itemUtil.GetFromDb(x, itemDict))

  return qI
}

export const get = (dict) => {
  var qI = new Base()

  qI.Dict = dict

  return qI
}

// api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData)

  apiFunc()
    .then((response) => response.data)
    .then((data) => {
      if (handle) {
        handle(data)
      }
    })
}

// Property
export const GetItemDict = (q) => {
  return itemDictUtil.get(q)
}

// view
export const GetView = (q) => {
  return (
    <div
      key={q.id}
      className="cv__skill__group__wrapper
    flex flex-col pl-4 pr-5 pt-3 pb-3 my-3"
    >
      {q.Name ? (
        <div className="text-base font-semibold mb-1.5">{q.Name}</div>
      ) : (
        ''
      )}
      <div>{q.ItemDict.View}</div>
    </div>
  )
}
