import { makeObservable, observable, action } from 'mobx'
import moment from 'moment'
import * as util from './CalendarInputUtil'
import CalendarInputView from './CalendarInputView'
import * as pt from './PickType'

export class CalendarInput {
  constructor() {
    makeObservable(this, {
      IsFocus: observable,
      pickedDate_Moment: observable,
      // pickedDate : observable
    })
  }

  // ref
  refData
  get RefData() {
    if (!this.refData) {
      this.refData = util.GetRefData(this)
    }
    return this.refData
  }

  ClickOutside

  // focus
  IsFocus = undefined

  //
  PickType = pt.day
  // PickType = pt.week

  pickedDate
  get PickedDate() {
    if (!this.pickedDate && !this.IsSet_PickedDate) {
      this.pickedDate = util.GetPickedDate(this)
    }
    return this.pickedDate
  }
  set PickedDate(v) {
    this.IsSet_PickedDate = true

    var calendar = this.Calendar
    calendar.CurrentDay = v ? moment(v) : v
  }

  pickedDate_Moment = undefined
  get PickedDate_Moment() {
    if (!this.pickedDate_Moment) {
      this.pickedDate_Moment = this.PickedDate
        ? moment(this.PickedDate)
        : undefined
    }
    return this.pickedDate_Moment
  }
  set PickedDate_Moment(v) {
    this.pickedDate_Moment = v

    // pickdate
    var pickedDate = (this.pickedDate = v ? v.toDate() : v)

    if (this.OnSet_PickedDate) {
      this.OnSet_PickedDate(pickedDate)
    }

    if (this.OnSetValue) {
      this.OnSetValue(pickedDate)
    }
  }

  Set_PickedDate = (v) => {
    this.PickedDate_Moment = v

    if (this.Set_ByInput) {
      this.Set_ByInput = false
    } else {
      this.OnPending = true
      this.PickedDate_TextVM.Content = util.GetPickedDate_Text(this)
    }
  }

  // text
  textFormat
  get TextFormat() {
    if (!this.textFormat) {
      this.textFormat = util.GetTextFormat(this)
    }
    return this.textFormat
  }

  pickedDate_Text
  get PickedDate_Text() {
    if (!this.pickedDate_Text) {
      this.pickedDate_Text = util.GetPickedDate_Text(this)
    }
    return this.pickedDate_Text
  }
  set PickedDate_Text(v) {
    if (!this.Set_PickedDate_Text(v)) {
      return
    }

    this.pickedDate_Text = v
  }

  Set_PickedDate_Text = (v) => {
    var temp = this.Temp_PickedDate_Text
    if (v !== temp) {
      this.PickedDate_TextVM.Content = temp
      return false
    }

    return true
  }

  pickedDate_TextVM
  get PickedDate_TextVM() {
    if (!this.pickedDate_TextVM) {
      this.pickedDate_TextVM = util.GetPickedDate_TextVM(this)
    }
    return this.pickedDate_TextVM
  }

  // calendar
  calendar
  get Calendar() {
    if (!this.calendar) {
      this.calendar = util.GetCalendar(this)
    }
    return this.calendar
  }

  IsAllowUndefined

  Ready = () => {
    if (this.PreReady) {
      if (!this.PreReady()) return
    }

    this.PickedDate_TextVM.Ready()
  }

  NotReady = () => {
    if (this.PreNotReady) {
      if (!this.PreNotReady()) return
    }

    this.PickedDate_TextVM.NotReady()
  }

  Edit = () => {
    if (this.PreEdit) {
      if (!this.PreEdit()) return
    }

    this.PickedDate_TextVM.Edit()
  }

  EditOK = () => {
    if (this.PreEditOK) {
      if (!this.PreEditOK()) return
    }

    this.IsFocus = false
    this.PickedDate_TextVM.EditOK()
  }

  // View
  get View() {
    return <CalendarInputView key={this.id} store={this} />
  }
}
