import { makeObservable, observable, action } from 'mobx'
import moment from 'moment'
import CalendarView from './CalendarView'
import * as util from './CalendarUtil'

import * as genUtil from '../../../util/generalUtil'
import * as dateUtil from '../../../util/dateUtil'

export class Calendar {
  constructor() {
    makeObservable(this, {
      currentDay: observable,
      calendarDict: observable,
    })
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID()
    }
    return this.tempId
  }

  get id() {
    return this.TempId
  }

  ShowWeek = true

  // day
  currentDay = undefined
  get CurrentDay() {
    if (!this.currentDay) {
      this.currentDay = moment()
    }
    return this.currentDay
  }
  set CurrentDay(v) {
    this.currentDay = v
    util.Handle_CalendarDict(this)

    if (this.OnSet_CurrentDay) {
      this.OnSet_CurrentDay(v)
    }
  }

  IsValid_SetCurrent

  SetCurrentDay = (day) => {
    if (this.IsValid_SetCurrent) {
      if (!this.IsValid_SetCurrent(day)) {
        return
      }
    }

    this.CurrentDay = day
  }

  // month
  get CurrentMonth() {
    return this.CurrentDay.format('MMMM')
  }

  PreviousMonth = () => {
    this.CurrentDay = this.CurrentDay.clone().subtract(1, 'month')
  }

  NextMonth = () => {
    this.CurrentDay = this.CurrentDay.clone().add(1, 'month')
  }

  // year
  get CurrentYear() {
    return this.CurrentDay.format('YYYY')
  }

  calendarDict = undefined
  get CalendarDict() {
    if (!this.calendarDict) {
      util.Handle_CalendarDict(this)
    }
    return this.calendarDict
  }
  set CalendarDict(v) {
    this.calendarDict = v
  }

  // css
  GetDayCss = (day) => {
    var qI = ''

    if (day.isSame(this.CurrentDay, 'day')) {
      qI += ' selected'
    }

    if (dateUtil.isToday(day)) {
      qI += ' today'
    }

    if (
      day.isBefore(this.CurrentDay, 'month') ||
      day.isAfter(this.CurrentDay, 'month')
    ) {
      qI += ' notsame-month'
    }

    if (this.IsValid_SetCurrent) {
      if (!this.IsValid_SetCurrent(day)) {
        qI += ' disabled'
      }
    }

    if (this.PostDayCss) {
      qI = this.PostDayCss(qI, day)
    }

    return qI
  }

  // style
  style
  get Style() {
    if (!this.style) {
      this.style = util.GetStyle(this)
    }
    return this.style
  }
  set Style(v) {
    this.style = v
  }

  // View
  get View() {
    return <CalendarView key={this.id} store={this} />
  }
}
