export const RemoveTime = (dateTime) => {
  var year = dateTime.getFullYear()
  var month = dateTime.getMonth()
  var date = dateTime.getDate()

  return new Date(year, month, date)
}

export const ConvertToDate = (dateString, format = 'dd/MM/yyyy') => {
  if (!dateString) return undefined;

  var refinedString
  switch (format) {
    case 'dd/MM/yyyy':
      var q1 = dateString.split('/')
      if (q1.length !== 3) {
        return undefined;
      }

      if (!Is_DayString_Valid(q1[0])
      || !Is_MonthString_Valid(q1[1])
      || !Is_YearString_Valid(q1[2])){
        return undefined
      }
      
      refinedString = q1.reverse().join('-')
      break;
    default:
      refinedString = dateString;
      break;
  }

  return new Date(refinedString)
}

const Is_DayString_Valid = (dayS) => {
  if (dayS.length < 1 || dayS.length > 2) return false;
  if (isNaN(dayS)) return false

  var day = parseInt(dayS)
  if (day < 1 || day > 31) return false;
  return true;
}

const Is_MonthString_Valid = (monthS) => {
  if (monthS.length < 1 || monthS.length > 2) return false;
  if (isNaN(monthS)) return false

  var month = parseInt(monthS)
  if (month < 1 || month > 12) return false;
  return true;
}

const Is_YearString_Valid = (yearS) => {
  if (yearS.length !== 4) return false;
  if (isNaN(yearS)) return false

  return true;
}

export const ConvertToString = (date, format) => {
  if (!date) return undefined

  var weekday = date.toLocaleDateString("en-US", { weekday: 'short' })
  var day = date.toLocaleDateString("en-US", { day: 'numeric' })
  var month = date.toLocaleDateString("en-US", { month: '2-digit' })
  var year = date.toLocaleDateString("en-US", { year: 'numeric' })

  switch(format){
    case 'dd/MM/yyyy':
    default:
      return `${day}/${month}/${year}`;
  }
}

// 25/10 function
export const isBeforeToday = (day) => {
  return day.isBefore(new Date(), 'day')
}

export const isToday = (day) => {
  return day.isSame(new Date(), 'day')
}