import { makeObservable, observable, action } from 'mobx'
import * as util from './PMDataUtil'
import * as genUtil from '../../../../util/generalUtil'

export class PMData {
  constructor() {
    makeObservable(this, {
      Language: observable,
    })
  }

  static instance
  static get Instance() {
    if (!this.instance) {
      this.instance = util.get()
    }
    return this.instance
  }
  static set Instance(v) {
    this.instance = v
  }

  //
  IsPublish = false

  UpdatedDate = '26/01/2022 11:30'

  Language = 'en'

  // route
  routeData = undefined
  get RouteData() {
    if (!this.routeData) {
      this.routeData = ['home']
    }
    return this.routeData
  }
  set RouteData(v) {
    this.routeData = v
  }

  // Data
  // info
  info
  get Info() {
    if (!this.info) {
      this.info = util.GetInfo(this)
    }
    return this.info
  }

  education
  get Education() {
    if (!this.education) {
      this.education = util.GetEducation(this)
    }
    return this.education
  }

  workHistory
  get WorkHistory() {
    if (!this.workHistory) {
      this.workHistory = util.GetWorkHistory(this)
    }
    return this.workHistory
  }

  skill
  get Skill() {
    if (!this.skill) {
      this.skill = util.GetSkill(this)
    }
    return this.skill
  }

  // duration
  GetDuration = () => {
    if (this.StartTime === undefined) {
      this.StartTime = performance.now()

      return 0
    }

    return performance.now() - this.StartTime
  }

  ResetDuration = () => {
    this.StartTime = undefined
  }
}
