import { Layout } from './Layout'

import * as headerUtil from '../header/HeaderUtil'
import * as bodyUtil from '../body/BodyUtil'
import * as footerUtil from '../footer/FooterUtil'

import { PMDataVM } from '../../../single/form/PMDataVM'
import { PMData } from '../../../single/model/PMData'
import * as genUtil from '../../../../../util/generalUtil'
import { message } from 'antd'

const pmDataVM = () => PMDataVM.Instance
const pmData = () => PMData.Instance

// Instance
export const get = () => {
  var qI = new Layout()

  return qI
}

// Property

export const GetHeader = (q) => {
  return headerUtil.get(q)
}

export const GetBody = (q) => {
  return bodyUtil.get(q)
}

export const GetFooter = (q) => {
  return footerUtil.get(q)
}
