import { PMData } from './PMData'
import * as genUtil from '../../../../util/generalUtil'
import * as api from '../../../../util/apiUtil'

import * as infoUtil from '../../entity/info/BaseUtil'
import * as educationUtil from '../../entity/history/education/dict/DictUtil'
import * as skillUtil from '../../entity/skill/group/dict/DictUtil'

// Instance
export const get = () => {
  var qI = new PMData()

  return qI
}

// Property
export const GetInfo = (q) => {
  return infoUtil.get(q)
}

export const GetEducation = (q) => {
  return educationUtil.GetFromData({ type: 'education' }, q)
}

export const GetWorkHistory = (q) => {
  return educationUtil.GetFromData({ type: 'work' }, q)
}

export const GetSkill = (q) => {
  return skillUtil.get(q)
}
