import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../util/generalUtil'
import ImageView from './ImageView';

export class Image {
  constructor() {
    makeObservable(this, {
      Source: observable,
      Alt: observable,
      width: observable,
      height: observable
    })
  }

  _id

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }


  Source = undefined

  Alt = undefined

  width = undefined
  get Width() {
    if (!this.width) {
      this.width = this.Height
    } return this.width
  } set Width(v) {
    this.width = v
  }

  height = undefined
  get Height() {
    if (!this.height) {
      this.height = this.Width
    } return this.height
  } set Height(v) {
    this.height = v
  }

  get View() {
    return <ImageView key={this.id} store={this} />
  }
}