// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined
  q.IsGet_Items = true

  const setObj = (data) => {
    q.Items = data.map((x) => itemUtil.GetFromDb(x, q))
    q.IsGet_Items = false
  }

  // setObj(
  //   GetTest()
  // );

  switch (q.Type) {
    case 'education':
      setObj(GetEducation())
      break
    case 'work':
      setObj(GetWork())
      break
  }

  // const getApi = () => {
  //   var apiData = {

  //   }

  //   itemUtil.GetData(apiData, (data) => {
  //     setObj(data)
  //   })
  // }

  // getApi()
}

// title
export const GetTitle = (q) => {
  var lang = pmData().Language

  return q.Type === 'education'
    ? { vi: 'Gio dc', en: 'Education' }[lang]
    : { vi: 'Kinh nghim lm vic', en: 'Work History' }[lang]
}

// view
export const GetView = (q) => {
  return (
    <div key={q.Type} className="ml-4 mr-3 mt-4">
      <div className="text-base font-semibold mb-4">{q.Title}</div>

      <div>{q.Items?.map((x) => x.View)}</div>
    </div>
  )
}

