import { url } from './Base_Url'
import { Base } from './Base'
import './Base.css'

import * as api from '../../../../../../util/apiUtil'
import { Progress } from 'antd'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict)

  qI._id = data._id
  qI.Name = data.name
  qI.Percent = data.percent

  return qI
}

export const get = (dict) => {
  var qI = new Base()

  qI.Dict = dict

  return qI
}

// api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData)

  apiFunc()
    .then((response) => response.data)
    .then((data) => {
      if (handle) {
        handle(data)
      }
    })
}

// Property

// view
export const GetView = (q) => {
  return (
    <div key={q.id} className="flex flex-col my-1">
      <div>
        <div className="flex -mb-1">
          <span className="flex-1">{q.Name}</span>
          <span>{q.Percent} %</span>
        </div>

        <Progress
          percent={q.Percent}
          showInfo={false}
          strokeColor={'#ffc107'}
          strokeWidth={3}
          trailColor={'#000'}
        />
      </div>
    </div>
  )
}
