import { makeObservable, observable, action } from 'mobx'
import { PMData } from '../model/PMData'
import * as util from './PMDataVMUtil'
import * as genUtil from '../../../../util/generalUtil'
import { Route, BrowserRouter, HashRouter, Switch } from 'react-router-dom'
import PMDataView from './PMDataView'

export class PMDataVM {
  constructor() {
    makeObservable(this, {
      isAdmin: observable,
      routeData: observable,
    })
  }

  static instance
  static get Instance() {
    if (!this.instance) {
      this.instance = util.get()
    }
    return this.instance
  }
  static set Instance(v) {
    this.instance = v
  }

  get pmData() {
    return PMData.Instance
  }

  get UpdatedDate() {
    return this.pmData.UpdatedDate
  }

  // Layout
  layout
  get Layout() {
    if (!this.layout) {
      this.layout = util.GetLayout(this)
    }
    return this.layout
  }

  OnSet_IsAdmin_Func_Dict = []

  // User
  isAdmin = undefined
  get IsAdmin() {
    return this.isAdmin
  }
  set IsAdmin(v) {
    this.isAdmin = v

    genUtil.Handle(
      this.OnSet_IsAdmin_Func_Dict,
      (v) => (this.OnSet_IsAdmin_Func_Dict = v)
    )
  }

  // Route
  routeData = undefined
  get RouteData() {
    if (!this.routeData) {
      this.routeData = this.pmData.RouteData
    }
    return this.routeData
  }
  set RouteData(v) {
    this.routeData = v
    this.pmData.RouteData = v
  }

  // View
  // ViewType = vt.home

  get MainView() {
    return <PMDataView store={this} />
  }

  get WrapperView() {
    return this.pmData.IsDeploy ? (
      <HashRouter basename={process.env.PUBLIC_URL}>
        <Switch>{this.MainView}</Switch>
      </HashRouter>
    ) : (
      <BrowserRouter>
        <Switch>{this.MainView}</Switch>
      </BrowserRouter>
    )
  }

  // View
  UnAuthorized_View = (
    <span className="text-[#ff4d4f] font-medium text-sm">
      Bn khng c quyn truy cp View hin ti!
    </span>
  )

  // Data
  get Info() {
    return this.pmData.Info
  }

  get Education() {
    return this.pmData.Education
  }

  get WorkHistory() {
    return this.pmData.WorkHistory
  }

  get Skill() {
    return this.pmData.Skill
  }
}
