import React, { Component } from 'react'
import { observer } from 'mobx-react'
import { CalendarInput } from './CalendarInput'
import { Button, Popover } from 'antd'
import InputView from '../../input/InputView'
import './CalendarInput.css'

import * as refUtil from '../../../util/refUtil'
import moment from 'moment'

class View extends Component {
  constructor(props) {
    super(props)

    var { store } = this.props
    if (!store) {
      var obj = (this.state = new CalendarInput())
      obj.PickedDate_TextVM.RenderState = 'edit'
    } else {
      this.state = store
    }
  }

  componentDidMount() {
    refUtil.Export_RefData(this.state, 'mount')
  }

  componentWillUnmount() {
    refUtil.Export_RefData(this.state, 'unmount')
  }

  render() {
    var data = this.state

    return (
      <>
        <div ref={data.RefData.GetRef('Target')}>
          <Popover
            content={
              <div ref={data.RefData.GetRef('Calendar')}>
                {data.Calendar.View}

                <div className="flex">
                  <div className="flex-1" />
                  <Button
                    className="float-right"
                    type="primary"
                    size="small"
                    onClick={() => (data.Calendar.CurrentDay = moment())}
                  >
                    Today
                  </Button>
                </div>
              </div>
            }
            visible={data.IsFocus}
            placement="bottomLeft"
          >
            <div
              ref={data.RefData.GetRef('Input')}
              className="calendar__text"
              onClick={() => {
                if (data.PickedDate_TextVM.RenderState !== 'default') {
                  data.IsFocus = true
                }
              }}
            >
              {data.PickedDate_TextVM.View}
            </div>
          </Popover>
        </div>
      </>
    )
  }
}

export default observer(View)
