import React, { Component } from 'react'
import { observer } from 'mobx-react'
import { Calendar } from './Calendar'
import moment from 'moment'
import './Calendar.css'

import * as genUtil from '../../../util/generalUtil'

class View extends Component {
  constructor(props) {
    super(props)

    var { store } = this.props
    if (!store) {
      this.state = new Calendar()
    } else {
      this.state = store
    }
  }

  render() {
    var data = this.state

    var style = data.Style
    var { width, item_height } = style

    return (
      <>
        <div key={data.id} className="calendar" style={{ width }}>
          <div className="header">
            <div className="previous" onClick={data.PreviousMonth}>
              {String.fromCharCode(171)}
            </div>

            <div className="current">
              {data.CurrentMonth} {data.CurrentYear}
            </div>

            <div className="next" onClick={data.NextMonth}>
              {String.fromCharCode(187)}
            </div>
          </div>
          <div className="body">
            {/* Week */}
            {data.ShowWeek ? (
              <div className="item week partion dayname">W</div>
            ) : (
              ''
            )}

            {/* Day */}
            {/* <div className="day-names"> */}
            {['S', 'M', 'T', 'W', 'T', 'F', 'S'].map((d, i) => (
              <div
                key={i}
                className={`item day partion dayname ${
                  data.ShowWeek ? 'showweek' : ''
                }`}
                style={{ height: item_height, lineHeight: item_height }}
              >
                {d}
              </div>
            ))}
            {/* </div> */}

            <div className="days">
              {data.CalendarDict.calendar.map((week) => {
                return (
                  <div key={genUtil.randomId()}>
                    {/* Week */}
                    {data.ShowWeek ? (
                      <div className="item week partion">{week.week}</div>
                    ) : (
                      ''
                    )}

                    {/* Day */}
                    {week.days.map((day) => (
                      <div
                        key={genUtil.randomId()}
                        className={`item day partion ${
                          data.ShowWeek ? 'showweek' : ''
                        } ${data.GetDayCss(day)}`}
                        onClick={() => data.SetCurrentDay(day)}
                        style={{
                          height: item_height,
                          lineHeight: item_height,
                        }}
                      >
                        {day.format('D')}
                      </div>
                    ))}
                  </div>
                )
              })}
            </div>
          </div>
        </div>

        <div className="border"></div>
      </>
    )
  }
}

export default observer(View)
