import { TextVM } from '../../text/TextVM'
import { Calendar } from '../base/Calendar'
import * as pt from './PickType'

import * as refUtil from '../../../util/refUtil'
import * as inputUtil from '../../../util/inputUtil'
import * as genUtil from '../../../util/generalUtil'
import { EditableTextVM } from '../../text/EditableTextVM'
import moment from 'moment'

// property
// pickedate
export const GetPickedDate = (q) => {
  switch (q.PickType) {
    case pt.day:
      return new Date()
    case pt.week:
      return moment().startOf('week').add(1, 'd').toDate()
  }
}

// ref
export const GetRefData = (q) => {
  var inputRef = refUtil.GetRefItem(q, 'Input').value
  var calendarRef = refUtil.GetRefItem(q, 'Calendar').value

  // click outside khi edit
  refUtil.ClickOutsite(q, 'Calendar', (e) => {
    if (refUtil.CheckOutside(e, inputRef)) {
      q.IsFocus = false
    }
  })

  if (q.ClickOutside) {
    // click outste i vi div tng (Target)
    refUtil.ClickOutsite(q, 'Calendar', (e) => {
      if (
        refUtil.CheckOutside(e, calendarRef) &&
        refUtil.CheckOutside(e, inputRef)
      ) {
        q.ClickOutside()
      }
    })
  }

  return q.Target_RefData
}

// text
export const GetTextFormat = (q) => {
  switch (q.PickType) {
    case pt.day:
      return 'DD/MM/YYYY'
    case pt.week:
      return '[T]W:DD/MM/YYYY'
  }
}

export const GetPickedDate_Text = (q) => {
  return q.PickedDate_Moment
    ? q.PickedDate_Moment.format(q.TextFormat)
    : undefined
}

export const GetCalendar = (q) => {
  var qI = new Calendar()
  qI.Dict = q

  qI.CurrentDay = q.PickedDate_Moment
  qI.OnSet_CurrentDay = (v) => {
    if (q.OnPending) {
      q.OnPending = false
    } else {
      q.Set_PickedDate(v)
    }
  }

  if (q.PickType === 'week') {
    qI.IsValid_SetCurrent = (day) => {
      return day.day() === 1
    }

    qI.PostDayCss = (v, day) => {
      if (day.day() === 1) {
        return `${v} border`
      }
      return v
    }
  }

  return qI
}

export const GetPickedDate_TextVM = (q) => {
  q.Temp_PickedDate_Text = q.PickedDate_Text

  var qI = inputUtil.GetInput(q, 'PickedDate_Text')

  // disable
  qI.IsDisabled = q.PickType === 'week'

  qI.Placeholder = q.Placeholder ? q.Placeholder : 'pick date'
  // qI.OnFocus = () => q.IsFocus = true
  qI.onSetContent = (v) => {
    var date
    if (!v || v.length === 0) {
      if (!q.IsAllowUndefined) {
        return
      }
    } else {
      date = moment(
        v,
        ['D/M/YYYY', 'D/MM/YYYY', 'DD/M/YYYY', 'DD/MM/YYYY'],
        true
      )
      if (!date._isValid) {
        return
      }
    }

    q.Temp_PickedDate_Text = v

    if (!q.OnPending) {
      q.Set_ByInput = true
    }

    q.Calendar.CurrentDay = date
  }

  return qI
}
