import { url } from './Base_Url'
import { Base } from './Base'
import './Base.css'

import * as api from '../../../../../../util/apiUtil'
import { PMData } from '../../../../single/model/PMData'

const pmData = () => PMData.Instance

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict)

  qI._id = data._id
  qI.Name = data.name
  qI.Organization = data.organization
  qI.Duration = data.duration
  qI.Detail = data.detail

  return qI
}

export const get = (dict) => {
  var qI = new Base()

  qI.Dict = dict

  return qI
}

// Property

// view
export const GetView = (q) => {
  var lang = pmData().Language

  return (
    <div
      key={q.id}
      className="cv__history__item__wrapper
    flex flex-col p-4 my-4"
    >
      <div className="flex items-center overflow-hidden overflow-clip whitespace-nowrap">
        <span className="text-base font-medium mb-0.5 flex-1">
          {q.Name[lang] ? q.Name[lang] : q.Name}
        </span>
        <div
          className="bg-[#20202a] sc-color py-1 px-2 rounded-xl text-xs
        cv__history__item__date"
        >
          {q.Duration}
        </div>
      </div>
      <span className="sc-color italic">
        {q.Organization[lang] ? q.Organization[lang] : q.Organization}
      </span>

      {q.Detail ? (
        <div className="sc-color mt-4">
          {q.Detail[lang] ? q.Detail[lang] : q.Detail}
        </div>
      ) : (
        ''
      )}
    </div>
  )
}

// api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData)

  apiFunc()
    .then((response) => response.data)
    .then((data) => {
      if (handle) {
        handle(data)
      }
    })
}
