import { Dict } from './Dict'
import * as itemUtil from '../base/BaseUtil'
import { CheckOutlined } from '@ant-design/icons'

// Instance
export const GetFromData = (data, dict) => {
  var qI = get(dict)

  qI._id = data.type
  qI.Type = data.type

  return qI
}

export const get = (dict) => {
  var qI = new Dict()

  return qI
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined
  q.IsGet_Items = true

  const setObj = (data) => {
    q.Items = data.map((x) => itemUtil.GetFromDb(x, q))
    q.IsGet_Items = false
  }

  setObj(GetTest())

  // const getApi = () => {
  //   var apiData = {

  //   }

  //   itemUtil.GetData(apiData, (data) => {
  //     setObj(data)
  //   })
  // }

  // getApi()
}

// view
export const GetView = (q) => {
  return (
    <div key={q.id} className="flex flex-col">
      {q.Items?.map((x) => x.View)}
    </div>
  )
}

